.main {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cont {
  margin: 0 auto;
  max-width: 1440px;
  width: 120%;
  padding: 0px 15px;
}

section {
  position: relative;
}

.section-form {
  display: flex;
  justify-content: center;
