.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  font-weight: 500;
  font-size: min(max(
						16px + 4 *
							(
								(100vw - 960px) / 240
							)
					, 16px), 20px);
  line-height: 23px;
  text-align: center;
  letter-spacing: -0.5625px;
  color: #ffffff;
  background: #00c38b;
  border-radius: min(max(
						10px + 10 *
							(
								(100vw - 960px) / 240
							)
					, 10px), 20px);
  border: none;
  cursor: pointer;
  padding: min(max(
						13px + 13.5 *
							(
								(100vw - 960px) / 240
							)
					, 13px), 26.5px);
  transition: all 0.4s ease;
}
.btn:hover {
  background-color: #084b3e;
}

input,
textarea {
  position: relative;
  width: 100%;
  resize: vertical;
  background-color: transparent;
  border-bottom: 1px solid #343434;
  padding: 13px 3px 13px 13px;
  transition: 0.3s ease-in-out all;
  font-family: var(--font-main);
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.206977px;
  color: #343434;
}
input::placeholder,
textarea::placeholder {
  font-weight: 400;
  font-size: 17px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.26);
}
input:focus,
textarea:focus {
  border-color: var(--color-green);
}
input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus, input:-webkit-autofill:active,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
textarea:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: #01013d !important;
}

textarea {
  height: 100px;
}

@keyframes jelly {
  from {
    transform: scale(1, 1);
  }
  30% {
    transform: scale(1.25, 0.75);
  }
  40% {
    transform: scale(0.75, 1.25);
  }
  50% {
    transform: scale(1.15, 0.85);
  }
  65% {
    transform: scale(0.95, 1.05);
  }
  75% {
    transform: scale(1.05, 0.95);
  }
  to {
    transform: scale(1, 1);
  }
}
.main-form__block {
  position: relative;
  margin-bottom: min(max(
						20px + 27 *
							(
								(100vw - 960px) / 240
							)
					, 20px), 47px);
  min-height: min(max(
						30px + 13 *
							(
								(100vw - 960px) / 240
							)
					, 30px), 43px);
}
.main-form__block:nth-child(3) {
  margin-bottom: 0;
}

.main-form__holder {
  position: absolute;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.206977px;
  color: #343434;
  pointer-events: none;
}

.main-form__holder span {
  color: #e33d5f;
}

.main-form__input {
  position: absolute;
  background-color: transparent;
  outline: none;
  height: auto;
  padding: 0px 0px min(max(
						10px + 14 *
							(
								(100vw - 960px) / 240
							)
					, 10px), 24px);
}

.main-form__input:focus ~ .main-form__holder {
  color: rgba(0, 195, 140, 0.2823529412);
}

.main-form__input:focus ~ .main-form__holder span {
  color: rgba(227, 61, 94, 0.431372549);
}

@media screen and (max-height: 800px) {
  .main {
    height: 100%;
  }
  .section-form {
    margin: 40px 0 40px;
  }
}
@media only screen and (max-width: 651px) {
  .main {
    height: 100%;
  }
  .main-form {
    flex-direction: column;
  }
  .main-form__top {
    max-width: 100%;
    border-radius: 16px;
    padding: 20px 20px 40px 20px;
  }
  .main-form__top h1 {
    margin-top: 20px;
  }
  .main-form__inner {
    padding: 30px 20px 30px 20px;
  }
}
@media only screen and (max-width: 480px) {
  .cont {
    padding: 0 10px;
  }
}