.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	font-weight: 500;
	font-size: adaptive-calc(20px, 16px);
	line-height: 23px;
	text-align: center;
	letter-spacing: -0.5625px;
	color: #ffffff;
	background: #00c38b;
	border-radius: adaptive-calc(20px, 10px);
	border: none;
	cursor: pointer;
	padding: adaptive-calc(26.5px, 13px);
	transition: all 0.4s ease;

	&:hover {
		background-color: #084b3e;
	}
}
