@use "sass:math";

html::-webkit-scrollbar,
html *::-webkit-scrollbar {
	width: 6px;
	height: 6px;
}

html::-webkit-scrollbar-track,
html *::-webkit-scrollbar-track {
	background-color: rgba(0, 0, 0, 0.2);
}

html::-webkit-scrollbar-thumb,
html *::-webkit-scrollbar-thumb {
	background-color: #91be7e;
}

.fp-watermark {
	display: none;
}

body {
	position: relative;
	min-width: 320px;
	margin: auto;
	background-color: #91be7e;
	font-family: var(--font-main);
	font-weight: 500;
	background-image: url(../images/background.jpg);
	background-repeat: no-repeat;
	background-size: cover;

	&.lock {
		overflow: hidden;
	}
}

/* #site {
	position: relative;
	overflow: hidden;
	min-height: 100%;
	display: flex;
	flex-direction: column;
} */

.main {
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
}

%default-transition {
	transition: all 0.3s;
}

.cont {
	margin: 0 auto;
	max-width: 1440px;
	width: perc(1440);
	padding: 0px 15px;
}

section {
	position: relative;
}

.section-form {
	display: flex;
	justify-content: center;
	/* margin: 115px 0 40px; */
}
.main-form {
	max-width: 1160px;
	width: 100%;
	display: flex;
	background-color: #fff;
	border-radius: adaptive-calc(50px, 16px);
	/* padding: 33.5px 51.5px 56px; */

	&__top {
		display: flex;
		flex-direction: column;
		position: relative;
		max-width: adaptive-calc(508px, 270px);
		border-radius: adaptive-calc(50px, 16px) 0px 0px adaptive-calc(50px, 16px);
		padding: 34.5px adaptive-calc(104px, 30px) 61px adaptive-calc(54px, 20px);
		z-index: 0;
		overflow: hidden;

		&::before {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			background: #084b3e;
			left: 0;
			top: 0;
			z-index: -1;
		}

		img {
			width: adaptive-calc(171.48px, 120px);
			margin-left: -2px;
		}
		h1 {
			margin-top: auto;
			font-weight: 500;
			font-size: adaptive-calc(39px, 24px);
			line-height: adaptive-calc(45px, 30px);
			letter-spacing: -1px;
			color: #f9e2ba;
		}
	}

	&__inner {
		width: 100%;
		padding: 53px adaptive-calc(150px, 20px) 58px adaptive-calc(52px, 20px);
	}

	&__inner-title {
		max-width: 424px;
		font-weight: 500;
		letter-spacing: -0.5px;
		font-size: adaptive-calc(34px, 24px);
		line-height: adaptive-calc(42px, 30px);
		color: #232323;
	}

	&__form {
		margin-top: adaptive-calc(78px, 30px);
	}

	&__btn {
		margin-top: adaptive-calc(50px, 30px);
	}

	&__links {
		margin-top: adaptive-calc(134px, 60px);
	}

	&__link-terms {
		font-weight: 600;
		font-size: 14px;
		line-height: 16px;
		letter-spacing: -0.5px;
		color: #bdbdbd;

		a {
			position: relative;
			color: #bdbdbd;
			transition: all 0.3s;

			&::after {
				content: "";
				position: absolute;
				bottom: 0;
				right: 0;
				width: 92%;
				height: 1px;
				background-color: #bdbdbd;
			}

			&:hover {
				color: #084b3e;
			}
		}
	}
	&__link-log {
		margin-top: 5px;
		font-weight: 600;
		font-size: 14px;
		line-height: 16px;
		letter-spacing: -0.5px;
		color: #bdbdbd;

		a {
			transition: all 0.3s;
			color: #19b07e;
			&:hover {
				color: #084b3e;
			}
		}
	}
}
