@font-face {
	font-family: "Campton";
	src: url("../fonts/Campton-Medium.woff2") format("woff2"),
		url("../fonts/Campton-Medium.woff") format("woff"),
		url("../fonts/Campton-Medium.ttf") format("truetype");
	font-weight: 500;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: "Campton";
	src: url("../fonts/Campton-SemiBold.woff2") format("woff2"),
		url("../fonts/Campton-SemiBold.woff") format("woff"),
		url("../fonts/Campton-SemiBold.ttf") format("truetype");
	font-weight: 600;
	font-style: normal;
	font-display: swap;
}
