// Default Input / Textarea

input,
textarea {
	position: relative;
	width: 100%;
	// resize: none;
	resize: vertical;
	background-color: transparent;
	border-bottom: 1px solid #343434;
	padding: 13px 3px 13px 13px;
	transition: 0.3s ease-in-out all;
	font-family: var(--font-main);
	font-weight: 500;
	font-size: 16px;
	line-height: 19px;
	letter-spacing: 0.206977px;
	color: #343434;

	&::placeholder {
		font-weight: 400;
		font-size: 17px;
		line-height: 20px;
		color: rgba(0, 0, 0, 0.26);
	}

	&:focus {
		border-color: var(--color-green);
	}

	@include transparent-bg-input();
}

textarea {
	height: 100px;
}

@keyframes jelly {
	from {
		transform: scale(1, 1);
	}

	30% {
		transform: scale(1.25, 0.75);
	}

	40% {
		transform: scale(0.75, 1.25);
	}

	50% {
		transform: scale(1.15, 0.85);
	}

	65% {
		transform: scale(0.95, 1.05);
	}

	75% {
		transform: scale(1.05, 0.95);
	}

	to {
		transform: scale(1, 1);
	}
}

.main-form__block {
	position: relative;
	margin-bottom: adaptive-calc(47px, 20px);
	min-height: adaptive-calc(43px, 30px);

	&:nth-child(3) {
		margin-bottom: 0;
	}
}
.main-form__holder {
	position: absolute;
	font-weight: 500;
	font-size: 16px;
	line-height: 19px;
	letter-spacing: 0.206977px;
	color: #343434;
	pointer-events: none;
}

.main-form__holder span {
	color: #e33d5f;
}

.main-form__input {
	position: absolute;
	background-color: transparent;
	outline: none;
	height: auto;
	padding: 0px 0px adaptive-calc(24px, 10px);
}
.main-form__input:focus ~ .main-form__holder {
	color: #00c38c48;
}

.main-form__input:focus ~ .main-form__holder span {
	color: #e33d5e6e;
}
